.square {
    min-width: 160px;
    min-height: 180px;
    font-size: 64px;
    text-align: center;
    color: #1CDBF5;
    font-weight: bolder;
    padding: 30px;
  }
  .b-r
  {
    border-right: 15px solid #ade257;
  }
  .b-b
  {
    border-bottom: 15px solid #ade257;
  }
  .color1 {
    color: #333a3b;
  }
  .color2 {
    color: rgb(206, 36, 36);
  }
  .square:hover{
    background-color: rgb(143, 211, 238);
  }
  @media only screen and (max-width: 600px){
    .square {
      min-width: 100px;
      min-height: 100px;
      font-size: 32px;
      text-align: center;
      font-weight: bolder;
      padding: 15px;
    }
    .b-r
    {
      border-right: 10px solid #ade257;
    }
    .b-b
    {
      border-bottom: 10px solid #ade257;
    }
    .color1 {
      color: #333a3b;
    }  
    .color2 {
      color: rgb(206, 36, 36);
    }
    .square:hover{
      background-color: rgb(143, 211, 238);
    }
    .container-fluid{
      height: 640px;
    }
  }
  .container-fluid{
    background: rgb(227,230,242);
    background: radial-gradient(circle, rgba(227,230,242,1) 0%, rgba(132, 195, 231, 0.903) 100%);
  }